body {
  font-family: -apple-system,BlinkMacSystemFont,"Helvetica Neue",sans-serif;
  background-color: #F1F3F5;
  letter-spacing: 0;
  font-weight: 400;
  font-style: normal;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-font-feature-settings: "liga" on;
  line-height: 1;
  color: #868E96; }

.truncate, .list-item-article h1, .list-item-article .reason {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.site-header {
  background-color: #fff;
  position: fixed;
  width: 100%;
  box-shadow: 0 1px 2px 0px #ddd;
  top: 0;
  z-index: 10; }

.site-header h1 {
  margin: 0;
  font-size: 0.9rem;
  padding-top: 1rem;
  padding-bottom: 1rem; }

.site-header h1 a {
  font-weight: 600;
  text-decoration: none;
  color: #C2255C;
  text-transform: uppercase; }

.container {
  padding-left: 1rem;
  padding-right: 1rem;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto; }

.list-container {
  margin-top: 4rem; }

.list {
  padding: 0; }

.list-item {
  list-style-type: none;
  margin-bottom: 1rem;
  width: 100%;
  transition: all 0.12s ease-in;
  transform: translate3d(0, 0, 0); }
  .list-item:hover {
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.12);
    transform: translate3d(0, -2px, 0); }
    .list-item:hover .list-item-article h2:after {
      opacity: 1;
      margin-left: 5px; }

.list-item a {
  display: block;
  background-color: #fff;
  padding: 1rem;
  text-decoration: none; }

.list-item a img {
  max-width: 100%;
  height: auto;
  max-height: 180px;
  transition: all 0.25s ease-in;
  opacity: 0; }

.list-item-article h1 {
  font-size: 0.9rem;
  font-weight: 400;
  color: #868E96;
  margin: 0 0 0.75rem 0;
  line-height: 1.2; }

.list-item-article h2 {
  font-size: 0.9rem;
  font-weight: 600;
  color: #C2255C;
  margin: 0; }
  .list-item-article h2:after {
    transition: all 0.25s ease-in;
    content: '→';
    opacity: 0; }

.list-item-article h3 {
  font-size: 0.75rem;
  font-weight: 200;
  color: #ADB5BD;
  margin: 0 0 0.5rem 0; }

.list-item-article figure {
  margin: 0;
  text-align: center;
  height: 180px; }

.list-item-article header {
  margin-top: 1rem; }

.list-item-article .reason {
  font-size: 0.9rem;
  margin: 0;
  margin-top: 0.75rem;
  font-style: italic;
  font-weight: normal;
  color: #ADB5BD;
  position: relative;
  border-top: 1px solid dashed #E9ECEF;
  padding-top: 0.75rem;
  font-family: Georgia, sans-serif;
  line-height: 1.2; }
  .list-item-article .reason:before {
    content: '“ ';
    font-family: Arial, sans-serif; }

.site-footer {
  text-align: center;
  padding-top: 2rem;
  padding-bottom: 2rem;
  font-size: 0.8rem; }
  .site-footer__inner {
    padding-top: 2rem;
    border-top: 1px solid #DEE2E6; }
    .site-footer__inner img {
      margin-bottom: 0.5rem; }
  .site-footer a {
    color: inherit;
    transition: color 0.25s ease; }
    .site-footer a:hover {
      color: #C2255C; }

@media screen and (min-width: 768px) {
  .list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
  .list-item {
    width: calc(50% - 0.5em); } }

@media screen and (min-width: 1024px) {
  .list-item {
    width: calc(33.33% - 1em); } }

@media screen and (min-width: 1280px) {
  .list-item {
    width: calc(25% - 0.75em); } }
